export default {
	data() {
		return {
			passWordShow: false,
			userName: '',
			passWord: ''
		};
	},
	onLoad:function(option){
		
	},
	methods:{
		// 清空用户名
		userNameEmpty() {
			this.userName = ''
		},
		// 密码显示或者隐藏
		passWordShowFun() {
			this.passWordShow = !this.passWordShow
		},
		 onerification(){
			 if (this.userName == '') {
				 uni.showToast({
				 	icon: 'none',
				 	title: '请输入用户名',
				 	duration: 2000
				 })
			 } else if (this.passWord == '') {
				 uni.showToast({
				 	icon: 'none',
				 	title: '请输入密码',
				 	duration: 2000
				 })
			 } else {
				 // 加载动画
				 uni.showLoading({
					 title: '登录中'
				 });
				this.$http.post(
					'Account/login',
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="username"' +
					'\r\n' +
					'\r\n' + this.userName +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="password"' +
					'\r\n' +
					'\r\n' + this.passWord +
					'\r\n--XXX--').then(res => {
					// 关闭加载动画
					uni.hideLoading();
					if (res.data.code == 200) {
						uni.setStorage({
							key: "userInfo",
							data: res.data.data
						})
						uni.setStorage({
							key: "token",
							data: res.data.data.token
						})
						 uni.showToast({
							icon: 'success',
							title: res.data.msg,
							duration: 2000,
							success: function () {
								uni.switchTab({
									url: "../dealer/index"
								})
							}
						 })
					} else {
						 uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 2000
						 })
					}
				})
			 }
		}
	}
}
